import React from 'react'
import { graphql } from 'gatsby'

// Styles for rendered markdown
import '../styles/index.css'

// material-ui
import Container from '@material-ui/core/Container'
import Toolbar from '@material-ui/core/Toolbar'
import Box from '@material-ui/core/Box'
import Paper from '@material-ui/core/Paper'
import Typography from '@material-ui/core/Typography'
import CardMedia from '@material-ui/core/CardMedia'
import Divider from '@material-ui/core/Divider'
import Fab from '@material-ui/core/Fab'
import KeyboardArrowUpIcon from '@material-ui/icons/KeyboardArrowUp'
import { withStyles } from '@material-ui/core'

// custom component
import SiteHeader from '../components/Headers/SiteHeader'
import ScrollTop from '../components/ScrollTop/ScrollTop'
import Footer from '../components/Footer/Footer'

const styles = theme => ({
  cardMedia: {
    height: '450px'
  },
  date: {
    paddingTop: theme.spacing(2),
    fontSize: 14
  },
  author: {
    fontSize: 14
  }
})

/**
 * Template component to creat about me page
 * @param {Object} props - component properties
 * @param {Object} props.classes - override the injected class names
 * @param {Object} props.data - containing the data for an article
 * @param {Object} props.location - representing where the app is from a router perspective
 * @returns {*}
 * @constructor
 */
function AboutPageTemplate({ classes, data, location, navigate }) {
  const { html, excerpt: description, frontmatter } = data.markdownRemark
  const {
    title,
    date,
    publishedBy,
    image: { publicURL }
  } = frontmatter

  return (
    <React.Fragment>
      <SiteHeader title={title} description={description} location={location} navigate={navigate} />
      {/* target for scrollTop component */}
      <Toolbar id="back-to-top-anchor" />
      <Box clone pt={8} pb={2}>
        <Container maxWidth="md">
          <Paper elevation={1}>
            <CardMedia className={classes.cardMedia} image={publicURL} />
            <Box pt={2} pr={2} pb={2} pl={2}>
              <div dangerouslySetInnerHTML={{ __html: html }} />
              <Box pt={1} pb={1}>
                <Divider variant="fullWidth" />
              </Box>
              <Typography variant="subtitle2" color="textSecondary">
                Published: {date}
              </Typography>
              <Typography variant="subtitle2" color="textSecondary">
                PublishedBy: {publishedBy}
              </Typography>
            </Box>
          </Paper>
        </Container>
      </Box>
      <ScrollTop>
        <Fab color="secondary" aria-label="scroll back to top">
          <KeyboardArrowUpIcon />
        </Fab>
      </ScrollTop>
      <Footer />
    </React.Fragment>
  )
}

/**
 * About page query
 */
export const query = graphql`
  query aboutTemplateQuery($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      frontmatter {
        title
        date(formatString: "YYYY-MM-DD")
        publishedBy
        image {
          publicURL
        }
      }
      excerpt(pruneLength: 160)
      html
    }
  }
`

export default withStyles(styles)(AboutPageTemplate)
